.article-tag{position:relative;display:inline-block;height:20px;padding:.2rem .5rem .2rem 1rem;margin:0 0.2rem;color:#fff;-webkit-clip-path:polygon(0 50%, 10px 0, 100% 0, 100% 100%, 10px 100%);clip-path:polygon(0 50%, 10px 0, 100% 0, 100% 100%, 10px 100%);line-height:20px}.article-tag::after{content:'';display:block;width:6px;height:6px;border-radius:50%;background-color:#fff;position:absolute;top:calc(50% - 3px);left:4px}



